import { Component } from '@angular/core';
import {
  IonicPage,
  NavController,
  Alert,
  AlertController
} from 'ionic-angular';
import { ProfileProvider } from '../../providers/profile/profile';
import { AuthProvider } from '../../providers/auth/auth';
@IonicPage()
@Component({
  selector: 'page-profile',
  templateUrl: 'profile.html',
})
export class ProfilePage {
  public userProfile: any;
  public dataNascimento: string;
  constructor(public navCtrl: NavController, public alertCtrl: AlertController,
    public authProvider: AuthProvider,
    public profileProvider: ProfileProvider) {
    }

  ionViewDidLoad() {
      this.profileProvider.getUserProfile().on('value', userProfileSnapshot => {
      this.userProfile = userProfileSnapshot.val();
      this.dataNascimento = userProfileSnapshot.val().Nascimento;
    }); 
  }

  logOut(): void {
    this.authProvider.logoutUser().then(() => {
      this.navCtrl.setRoot('LoginPage');
    });
  }

  updateName(): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Seu nome e sobrenome",
      inputs: [
        {
          name: 'Nome',
          placeholder: 'Seu nome',
          value: this.userProfile.Nome
        },
        {
          name: 'Sobrenome',
          placeholder: 'Seu sobrenome',
          value: this.userProfile.Sobrenome
        },
      ],
      buttons: [
        { text: 'Cancelar' },
        {
          text: 'Salvar',
          handler: data => {
            this.profileProvider.updateName(data.Nome, data.Sobrenome);
          }
        }
      ]
    });
    alert.present();
  }

  
  updateNiver(dataNascimento: string): void {
    this.profileProvider.updateNiver(dataNascimento);
  }


  updateCPF(): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Seu CPF",
      inputs: [
        {
          name: 'CPF',
          placeholder: 'Seu Cpf',
          value: this.userProfile.CPF
        }],
      buttons: [
        { text: 'Cancelar' },
        {
          text: 'Salvar',
          handler: data => {
            this.profileProvider.updateCPF(data.CPF);
          }
        }
      ]
    });
    alert.present();
  }


  updateRG(): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Seu RG",
      inputs: [
        {
          name: 'RG',
          placeholder: 'Seu RG',
          value: this.userProfile.RG
        }],
      buttons: [
        { text: 'Cancelar' },
        {
          text: 'Salvar',
          handler: data => {
            this.profileProvider.updateRG(data.RG);
          }
        }
      ]
    });
    alert.present();
  }


  updateTEL1(): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Telefone 1",
      inputs: [
        {
          name: 'Tel1',
          placeholder: 'Tel1',
          value: this.userProfile.Tel1
        }],
      buttons: [
        { text: 'Cancelar' },
        {
          text: 'Salvar',
          handler: data => {
            this.profileProvider.updateTEL1(data.Tel1);
          }
        }
      ]
    });
    alert.present();
  }


  updateTEL2(): void {
    const alert: Alert = this.alertCtrl.create({
      message: "Telefone 2",
      inputs: [
        {
          name: 'Tel2',
          placeholder: 'Tel2',
          value: this.userProfile.Tel2
        }],
      buttons: [
        { text: 'Cancelar' },
        {
          text: 'Salvar',
          handler: data => {
            this.profileProvider.updateTEL2(data.Tel2);
          }
        }
      ]
    });
    alert.present();
  }


  updateEmail(): void {
    let alert: Alert = this.alertCtrl.create({
      inputs: [
        {
          name: 'newEmail',
          placeholder: 'Your new email',
        },
        {
          name: 'password',
          placeholder: 'Your password',
          type: 'password'
        },
      ],
      buttons: [
        { text: 'Cancel' },
        {
          text: 'Save',
          handler: data => {
            this.profileProvider.updateEmail(data.newEmail, data.password)
              .then(() => {
                console.log("Email Changed Successfully");
              }).catch(error => {
                console.log('ERROR: ' + error.message);
              });
          }
        }
      ]
    });
    alert.present();
  }

  updatePassword(): void {
    let alert: Alert = this.alertCtrl.create({
      inputs: [
        {
          name: 'newPassword',
          placeholder: 'Your new password',
          type: 'password'
        },
        {
          name: 'oldPassword',
      placeholder: 'Your old password',
          type: 'password'
        },
      ],
      buttons: [
        { text: 'Cancel' },
        {
          text: 'Save',
          handler: data => {
            this.profileProvider.updatePassword(data.newPassword,
              data.oldPassword);
          }
        }
      ]
    });
    alert.present();
  }

}


